/* modal for project description editor */
.Modal {
	position: fixed;
	bottom: -150vh;
	background-color: #fff;
	width: 50%;
	height: auto;
	left: 50%;
	padding: 0 12px 12px;
	transition: bottom 500ms ease-out;
	z-index: 10;
  }

  .Modal.Show {
	bottom: 0;
  }
  .upcoming-list {
    display: grid;
    grid-template-columns: 33.33% 33.33% 33.33%;
    grid-column-gap: 20px;
}

.upcoming-list .inner-box .fa-trash {
    color: #fff !important;
    font-size: 20px;
    margin-right: 8px;
    display: inline-block;
	padding-left: 0;
}

.upcoming-all .inner-box .fa-trash {
    color: #fff !important;
    font-size: 20px;
    margin-right: 58px;
    display: inline-block;
	padding-left: 0;
}
.update__delete{
	display: flex;
	justify-content: center;
	justify-content: safe;
	max-width: 5px !important;
}

