.pool_detail_banner {
	background: url("../../images/banner.jpg");
	background-size: cover;
	display: grid;
	align-items: center;
	padding: 60px 0;
}
.Toastify__toast--default {
    background: #37419f !important;
    color: #fff;
 }
.Toastify__toast--info {
    background: #37419f !important;
 }
.ti_tle {
	display: grid;
	grid-template-columns: 100px auto;
	margin-bottom: 25px;
}
.ti_tle h3 {
	font-style: normal;
	font-weight: bold;
	font-size: 34px;
	line-height: 110%;
	letter-spacing: 0.02em;
	color: #ffffff;
	margin: 0px;
	margin: -3px 0 0 0;
}
.inner_pool_detail_banner {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-gap: 20px;
}
.ti_tle img {
	background: #fff;
	border-radius: 6px;
	max-width: 80%;
}
.ti_tle p {
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 150%;
	color: #fff;
}
.ti_tle .socia_l img:hover {
	background-color: #37419f !important;
	padding: 8px;
	height: 32px;
	width: 32px;
	text-align: center;
	max-width: 100%;
}

.ti_tle .socia_l img {
	background-color: #352e28;
	padding: 8px;
	height: 32px;
	width: 32px;
	text-align: center;
	max-width: 100%;
}

.ti_tle .socia_l a {
	padding: 0 5px;
}
.ti_tle .socia_l {
	display: flex;
	padding-top: 36px;
}
.tit_le2 {
	display: grid;
	grid-template-columns: 40px auto;
	margin-bottom: 25px;
}
.tit_le2 h3 span {
	font-style: normal;
	font-weight: normal;
	font-size: 14px;
	line-height: 110%;
	letter-spacing: 0.02em;
	color: #fff;
	opacity: 0.5;
	margin-top: 9px;
}
.tit_le2 h3 {
	font-style: normal;
	font-weight: 500;
	font-size: 16px;
	line-height: 110%;
	color: #fff;
	margin: 3px 0;
	letter-spacing: 0.02em;
}
.detail_card {
	padding: 30px;
	background: #242527;
	border: 1px solid rgba(255, 255, 255, 0.08);
	box-sizing: border-box;
	border-radius: 16px;
	margin-top: -9px;
}
.allocation > div:nth-child(2) {
	text-align: right;
}

.detail_card .allocation {
	grid-template-columns: auto auto;
}
.detail_card .progress {
	margin: 10px 0;
	padding: 0;
	width: 100%;
	height: 15px;
	overflow: hidden;
	background: rgba(255, 255, 255, 0.08);
	border-radius: 6px;
}
.detail_card .bar {
	position: relative;
	float: left;
	min-width: 1%;
	height: 100%;
	background: #37419f;
}

.gen_btnn {
	float: right;
	padding: 6px 7px;
	min-width: 100px;
	text-decoration: none;
	border-radius: 10px;
	font-family: Roboto;
	font-style: normal;
	font-size: 19px;
	line-height: 12px;
	text-align: center;
	letter-spacing: 0.02em;
	text-transform: uppercase;
	cursor: pointer;
	margin-right: -11px;
	margin-top: -1px;
}

.btnn_orange {
	background: #d37506;
	color: #fff;
	border-color: #d37506;
}
.btnn_orange:hover {
	background: #fff;
	color: #d37506;
}
.btnn_white {
	background: transparent;
	color: #fff;
	border-color: #fff;
}
.right_ban .btnn_white {
	font-size: 12px;
	border-radius: 6px !important;
	background-color: #37419f;
	border: #37419f;
}
.btnn_white:hover {
	background: #fff;
	color: #37419f;
	border-color: #37419f;
}
.detail_card .percent {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	margin: 0;
	font-family: tahoma, arial, helvetica;
	font-size: 12px;
	color: white;
}
.detail_card .percent {
	display: none;
}
.tit_row {
	display: grid;
	grid-template-columns: auto 100px;
	padding-right: 14px;
}
.detail_card .prog_bar span {
	font-style: normal;
	font-weight: normal;
	font-size: 12px;
	line-height: 14px;
	text-transform: capitalize;
	color: rgba(255, 255, 255, 0.5);
}
.detail_card .prog_bar_grd {
	display: grid;
	grid-template-columns: auto auto;
}
.detail_card .parti {
	text-align: right;
}
.detail_card .prog._percent {
	color: #37419f;
}
.detail_card .parti._nls {
	color: #37419f;
}
.detail_card .prog_bar {
	padding: 15px 0;
}
.detail_card .allocation {
	display: grid;
	grid-template-columns: auto auto;
	grid-gap: 10px;
}
.rts {
	text-align: right;
}
.bttn {
	float: right;
}
.socia_grd p {
	margin: 7px 0 0 0;
}
.border-left-radius {
	border-bottom-left-radius: 16px !important;
}

.border-right-radius {
	border-bottom-right-radius: 16px !important;
}
.timer_mobile{display:none;}
@media (max-width: 1024px) {
	.pool_grid {
		grid-template-columns: 1fr 1fr;
		grid-gap: 25px;
	}
	.banner {
		height: auto;
	}
	.inner_pool_detail_banner {
		grid-template-columns: auto;
	}
	.socia_grd {
		display: grid;
		grid-template-columns: 1fr 1fr;
		grid-gap: 25px;
	}
	.ti_tle .socia_l {
		display: flex;
		padding-top: 1px;
		flex-flow: row-reverse;
	}
	.ti_tle img {
		width: 100%;
	}
}

@media (max-width: 800px) {
	.inner_pool_detail_banner {
		display: grid;
		grid-template-columns: 1fr;
		grid-gap: 20px;
	}
}

@media (max-width: 767px) {
	.ti_tle .socia_l {
		flex-flow: wrap;
	}
	.tit_le2 h3 span {
		display: block;
	}
	.tit_le2 {
		margin-bottom: 4px;
	}
	.socia_grd {
		grid-template-columns: auto;
	}
	.ti_tle .socia_l {
		padding-top: 0px;
	}
	.pool_grid {
		grid-template-columns: auto;
		grid-gap: 25px;
	}
	.inner_header {
		display: block !important;
	}
	.banner h1 {
		font-size: 35px;
	}
	.logo {
		width: 155px;
		margin: 0 auto;
	}
	.navi_gation .gen_btn {
		padding: 10px 10px;
		min-width: 68px;
		font-size: 10px;
		line-height: 10px;
	}
	.navi_gation .gen_btn.btn_white {
		width: 200px;
		margin: 10px auto 0 !important;
		padding: 17px 0;
	}
	.allocation p {
		font-size: 12px;
	}
	.allocation h3 {
		font-size: 20px;
	}
	.inner_pool_details {
		grid-template-columns: auto;
	}
	.content_grid {
		grid-template-columns: auto;
	}
	.inner_pool_detail_banner .allocation .rts h3:hover::after { font-size: 18px;}
	.timer_desktop{
		display: none;
	}
	.timer_mobile{
		display: block;
	}
}
@media (max-width: 460px) {
	.logo img {
		width: 100%;
	}
	.inner_header {
		grid-template-columns: 130px auto;
	}
	.ti_tle {
		grid-template-columns: 65px auto;
	}
	.ti_tle img {
		background: #fff;
		border-radius: 6px;
		width: 73%;
	}
}
@media (max-width: 385px) {
	.navi_gation .gen_btn {
		padding: 4px 5px;
		font-size: 9px;
	}
	.navi_gation {
		display: grid;
		grid-gap: 4px;
	}
	.logo img {
		max-width: 130px;
	}
	.inner_header {
		grid-template-columns: auto 110px;
	}
	
}

.paper {
	position: absolute;
	width: 95%;
	max-width: 300px;
	height: 200;
	background-color: #fff;
	border: 2px solid #000;
	box-shadow: 5;
	padding: 2px 4px 3px;
	border-radius: 20px;
	text-align: center;
	padding: 0px 0 28px 0;
	height: auto;
	transform: none !important;
	left: 0 !important;
	right: 0;
	margin: auto;
	top: 25% !important;
}
.paper_button {
	background: #37419f;
	color: #fff;
	border: 1px solid #37419f;
	font-size: 16px;
	padding: 9px 50px;
	font-family: "Roboto";
	text-transform: "uppercase";
	border-radius: 6px;
	margin: auto;
	cursor: pointer;
	display: block;
}
.paper_h2 {
	background: #37419f;
	color: #fff;
	padding: 14px 0;
	border-radius: 16px 16px 0 0;
}
.paper_input {
	width: 75%;
	display: inline-block;
	padding: 8px 13px;
	border-radius: 6px;
	border: 1px solid #37419f;
	margin-top: 18px;
}



