.container_cust {
	max-width: 1280px;
	margin: auto;
	padding: 0 20px;
	width: 100%;
}
.Toastify__toast--info {
    background: #37419f !important;
 }
.gen_btn {
	display: inline-block;
	padding: 13px 15px;
	min-width: 226px;
	text-decoration: none;
	border-radius: 6px;
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	font-size: 12px;
	line-height: 14px;
	text-align: center;
	letter-spacing: 0.02em;
	text-transform: uppercase;
	border: 1px solid;
	cursor: pointer;
}

.gen_bttn {
	display: inline-block;
	padding: 13px 15px;
	min-width: 200px;
	text-decoration: none;
	border-radius: 6px;
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	font-size: 12px;
	line-height: 12px;
	text-align: center;
	letter-spacing: 0.02em;
	text-transform: uppercase;
	cursor: pointer;
}
.btn_orange {
	background: #37419f;
	color: #fff;
	border-color: #37419f;
}

.address {
	color: #fff;
}

.btn_orange:hover {
	background: #fff;
	color: #37419f;
}
.btn_white {
	background: transparent;
	color: #fff;
	border: 1px solid transparent;

}
.btn_white:hover {
	background: #fff;
	color: #37419f;
	border-color: #37419f;
}

.addressh {
	color: #fff;
	border: 2px solid #fff;
	padding: 8px 18px;
	border-radius: 5px;
	font-size: 18px;
	display: inline-block;
	background: black;
	cursor: pointer;
}

.hea_der {
	background: #18191b;
	padding: 0px 0;
}
.inner_header {
	display: grid;
	grid-template-columns: auto auto;
	align-items: center;
}
.navi_gation {
	text-align: right;
}
.yellow_btn {
	background: #37419f;
	color: #fff;
	border-color: #37419f;
}
.hea_der .btn_orange:hover {
	background: transparent;
	color: #fff;
	border-color: #fff;
}
.navi_gation .gen_btn.btn_white {
	margin-right: 10px;
}
.navi_gation .gen_btn.btn_white {
	display: none;
}
.hea_der .container_cust {
	padding: 0 4px;
}

p {
	font-weight: 300 !important;
}

/* modal */
.paper {
	position: absolute;
	width: 95%;
	max-width: 360px;
	height: 200;
	background-color: #fff;
	border: 2px solid #000;
	box-shadow: 5;
	padding: 2px, 4px, 3px;
	border-radius: 20px;
	text-align: center;
	padding: 0px 0 28px 0;
	height: auto;
	transform: none !important;
	left: 0 !important;
	right: 0;
	margin: auto;
	top: 25% !important;
}
.paper_button {
	background: #37419f;
	color: #fff;
	border: 1px solid #37419f;
	font-size: 16px;
	padding: 9px 50px;
	font-family: Roboto;
	text-transform: uppercase;
	border-radius: 6px;
	margin: auto;
	cursor: pointer;
}
.paper_h2 { 
	background: #37419f;
	color: #fff;
	padding: 14px 0;  
	border-radius: 16px 16px 0 0;
}
.paper_input {
	width: 95%;
	display: inline-block;
	padding: 8px 3px;
	border-radius: 6px;
	border: 1px solid #37419f;
	margin-top: 18px;
}
/* modal */







@media (max-width: 767px){
	header .inner_header {display: grid!important;grid-template-columns: 170px auto!important; align-items: center;padding-left: 9px;}
.navi_gation .gen_bttn {min-width: 100px !important;}
.navi_gation .gen_bttn.btn_white.connect_btn.yellow_btn {word-break: break-all; width: 93px; line-height: 22px; padding: 5px 11px 14px 11px; height: 33px !important;overflow: hidden;}
.navi_gation .gen_bttn { min-width: 68px !important;}
	}
	
	@media (max-width: 385px){
header .inner_header { grid-template-columns: 160px auto !important; display: grid !important;}
.navi_gation {justify-content: end!important;}
}